<template lang="html">
  <div class="page yo-flex">
    <v-header title="首页"></v-header>
    <v-content>
      <v-grid></v-grid>
    </v-content>
    <v-footer>
      <template v-for="item in footer">
        <footer-item :icon="item.icon" :text="item.text" :active="item.active" :link="item.link"></footer-item>
      </template>
    </v-footer>
  </div>
</template>

<script>
import Header from '../components/header/header';
import Content from '../components/content/index';
import Button from '../components/btn/btn';
import Grid from '../components/grid/grid';

import {
  Footer,
  FooterItem
} from '../components/footer/index';

export default {
  data() {
    return {
      msg: 'Welcome to use uikit',
      footer: [{
        icon: '&#xe680;',
        text:'搜索',
        active: true,
        link: 'search'
      }, {
        icon: '&#xe670;',
        text:'发现',
        active: false,
        link: 'find'
      }, {
        icon: '&#xe670;',
        text:'列表',
        active: false,
        link: 'list'
      }, {
        icon: '&#xe666;',
        text:'我的',
        active: false,
        link: 'side'
      }],
      show: true
    }
  },
  components: {
    'v-header': Header,
    'v-content': Content,
    'v-grid': Grid,
    'v-footer': Footer,
    'footer-item': FooterItem
  },
  methods: {
    close: function() {
      this.$data.show = false;
    },
    open:function(){
      this.$data.show = true;
    }
  }
}
</script>



