import BackLink from './directives/back-link'
import Swipe from './directives/swipe'
import Transitionend from './directives/transitionend'
import db from './db'
import Vue from 'vue'
/**
 * vue-router does not support reverse transition
 */
class RouterConfig {
	constructor(router) {
		this.router = router
	}
	config() {
		const router = this.router;

		this.router.beforeEach((to, from, next) => {
			try {
				const toPath = to.path;
        const fromPath = from.path;
				const scrollTop = router.app.$el.querySelector('.page-content').scrollTop;
				const h = db.get(toPath);
				if (h && h.history || (fromPath && fromPath.indexOf(to) === 0)) {
					router.app.$el.className = 'transition-reverse';
					h.history = false;
					db.set(to, h);
				} else {
					db.set(from, {
						scrollTop: scrollTop,
						history: true
					});
					router.app.$el.className = '';
				}
			} catch (e) {
				// swallo error
				console.error(e)
			}
			next()
		})
		this.router.afterEach((to, from, next) => {
			const h = db.get(to.path);
			if (h && h.scrollTop) {
				Vue.nextTick(() => {
					// const to = to.router.app.$el.querySelectorAll('.page-content')[1];
					if (to)
						to.scrollTop = h.scrollTop // TODO:
					})
			}
		})
	}
}
export default {
	install(Vue) {
		Vue.directive('back-link', BackLink)
		Vue.directive('swipe', Swipe)
		Vue.directive('transitionend', Transitionend)
	},
	router(router) {
		const rc = new RouterConfig(router)
		rc.config()
	}
}



