<template lang="html">
  <header class="yo-header">
    <slot name="regret">
      <a href="#" class="regret yo-ico" v-back-link>&#xe604;</a>
    </slot>
    <h2 class="title">{{title}}</h2>
    <slot name="affirm">
      <a href="#" class="affirm" v-on:click="affirm">{{btnTxt}}</a>
    </slot>
  </header>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '标题'
    },
    btnTxt:{
      type: String,
      default: '哦了'
    },
    affirm:{
      type: Function,
      default:function(){
        console.log('default');
      }
    }
  }
}
</script>



