

<template lang="html">

<router-link :to="{path:link}" v-bind:class="['item','item-y-ico',active?'item-on':'']">
    <slot>
        <i class="yo-ico" v-html="icon"></i> {{text}}
    </slot>
</router-link>

</template>

<script type="text/javascript">

export default {
    props: {
        icon: {
            type: String,
            default: '&#xf04b;'
        },
        text: {
            type: String,
            default: '导航'
        },
        active: {
            type: Boolean,
            default: false
        },
        link:{
          type:String
        }
    }
}

</script>



