

<template>
<div class="page yo-flex">
  <v-header title="首页" btn-txt="确定"></v-header>
  <v-content>
    <v-align>
      <v-btn txt="打开侧边设置" :click="btnClick"></v-btn>
    </v-align>
  </v-content>
  <v-footer>
    <template v-for="item in footer">
        <footer-item :icon="item.icon" :text="item.text" :active="item.active" :link="item.link"></footer-item>
</template>
  </v-footer>
  <v-sidepanel :show="show" v-on:pclose="close">
    <v-list>
        <v-item v-for="n in 10"></v-item>
        <v-item>
            <a class="yo-btn yo-btn-l yo-btn-stacked" @click="close">关闭</a>
        </v-item>
    </v-list>
  </v-sidepanel>
</div>

</template>

<script>
import Header from '../components/header/header';
import Content from '../components/content/index';
import SidePanel from '../components/sidePanel/sidepanel';
import Button from '../components/btn/btn';
import Align from '../components/align/align';

import {
  List,
  ListItem
}
from '../components/list/index';
import {
  Footer,
  FooterItem
}
from '../components/footer/index';

export default {
  data() {
    return {
      msg: 'Welcome to use uikit',
      footer: [{
        icon: '&#xe680;',
        text:'搜索',
        active: false,
        link: 'search'
      }, {
        icon: '&#xe670;',
        text:'发现',
        active: false,
        link: 'find'
      }, {
        icon: '&#xe670;',
        text:'列表',
        active: false,
        link: 'list'
      }, {
        icon: '&#xe666;',
        text:'我的',
        active: true,
        link: 'side'
      }],
      show: true,
      isTitle: false,
      ok() {
        console.log('i am ok');
      }
    }
  },
  components: {
    'v-header': Header,
    'v-content': Content,
    'v-sidepanel': SidePanel,
    'v-list': List,
    'v-item': ListItem,
    'v-btn': Button,
    'v-align': Align,
    'v-footer': Footer,
    'footer-item': FooterItem
  },
  methods: {
    close() {
      this.$data.show = false
    },
    btnClick() {
      this.$data.show = true;
    },
    open() {
      console.log('wi open');
    }
  }
}
</script>



