<template lang="html">

  <modal :show="show" v-on:pclose="close" class="yo-modal-bottom yo-modal-stretch-x">
    <div class="cont action ani">
        <div class="yo-actionsheet">
            <slot>
              <div class="menu">
                  <h3 class="title">保存图片？</h3>
                  <div class="item">存储图像</div>
                  <div class="item">拷贝</div>
              </div>
              <ul class="action">
                  <li class="item item-important" @click="close">取消</li>
              </ul>
            </slot>
        </div>
    </div>
  </modal>
</template>

<script type="text/javascript">
  import Modal from '../modal/modal';
  export default {
    props:{
      show:Boolean,
      default:false
    },
    components:{
      Modal
    },
    methods:{
      close:function(){
        this.$emit('pclose');
      }
    }
  }
</script>



