

<template lang="html">

<modal :show="show" v-on:pclose="close">
    <div class="yo-dialog ani dialog">
        <slot name="header" v-if="isTitle">
            <header class="hd">
                <h2 class="title">{{title}}</h2>
            </header>
        </slot>
        <slot name="content">
            <div class="bd">
                <p>{{content}}</p>
            </div>
        </slot>
        <slot name="footer">
            <footer class="ft">
                <button class="yo-btn yo-btn-dialog yo-btn-l" @click="_cancel">{{cancelTxt}}</button>
                <button class="yo-btn yo-btn-dialog yo-btn-l" @click="_ok">{{okTxt}}</button>
            </footer>
        </slot>
    </div>
</modal>

</template>

<script>
import Modal from '../modal/modal';

export default {
  components: {
    Modal
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '标题'
    },
    content: {
      type: String,
      default: '有title的dialog'
    },
    okTxt: {
      type: String,
      default: '确定'
    },
    cancelTxt: {
      type: String,
      default: '取消'
    },
    ok: {
      type: Function,
      default: (vm) => {
        if (vm) {
          vm.$emit('pclose')
        }
      }
    },
    cancel: {
      type: Function,
      default: (vm) => {
        if (vm) {
          vm.$emit('pclose')
        }
      }
    },
    isTitle: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    _ok() {
      if (this.ok && this.ok instanceof Function) {
        this.ok.call(null, this)
      } else {
        this.$emit('pclose')
      }
    },
    _cancel() {
      if (this.cancel && this.cancel instanceof Function) {
        this.cancel.call(null,this)
      } else {
        this.$emit('pclose')
      }
    },
    close:function(){
      this.$emit('pclose');
    }
  }
}
</script>



