<template lang="html">
  <section class="modal-wrap" v-if="showWrap" v-bind:class="show?'active':''">
    <div class="yo-modal ani" @click.self="close"></div>
    <slot></slot>
  </section>
</template>
<script type="text/javascript">
export default {
  props: {
    show: Boolean,
    default: false
  },
  data: function() {
    return {
      showWrap: this.show
    }
  },
  methods: {
    close() {
      this.$emit('pclose');
    }
  },
  mounted: function() {
    const _this = this;
    _this.$watch('show', function(newValue, oldValue) {
      if (newValue === false) {
        // css动画设置为300ms，为了确保setTimeout的不准确造成的点击卡顿，设置为100ms
        setTimeout(function() {
          _this.$data.showWrap = false;
        }, 150);
      } else {
        _this.$data.showWrap = true;
      }
    })
  }
}
</script>



