

<template>
<div class="page yo-flex">
  <v-header title="首页"></v-header>
  <v-content>
    <v-scroll :on-refresh="onRefresh" :on-infinite="onInfinite">
      <v-list>
        <v-item v-for="(item,index) in beginData" track-by="uid">{{index+1}}-{{item.txt}}-{{item.time}}</v-item>
        <v-item v-for="n in 12"></v-item>
        <v-item v-for="(item,index) in itemData" track-by="time">{{index+1}}-{{item.txt}}-{{item.time}}</v-item>
      </v-list>
    </v-scroll>
  </v-content>
  <v-footer>
    <template v-for="item in footer">
        <footer-item :icon="item.icon" :text="item.text" :active="item.active" :link="item.link"></footer-item>
</template>
  </v-footer>
</div>

</template>

<script>
import Header from '../components/header/header';
import Content from '../components/content/index';
import Scroll from '../components/scroll/scroll';

import {
  List,
  ListItem
}
from '../components/list/index';
import {
  Footer,
  FooterItem
}
from '../components/footer/index';

export default {
  data() {
    return {
      msg: 'Welcome to use uikit',
      footer: [{
        icon: '&#xe680;',
        text: '搜索',
        active: false,
        link: 'search'
      }, {
        icon: '&#xe670;',
        text: '发现',
        active: false,
        link: 'find'
      }, {
        icon: '&#xe670;',
        text: '列表',
        active: true,
        link: 'list'
      }, {
        icon: '&#xe666;',
        text: '我的',
        active: false,
        link: 'side'
      }],
      itemData: [{
        txt: '测试scroll',
        time: new Date()
      }],
      beginData: [],
      show: true,
      isTitle: false,
      ok() {
        console.log('i am ok');
      }
    }
  },
  components: {
    'v-header': Header,
    'v-content': Content,
    'v-scroll': Scroll,
    'v-list': List,
    'v-item': ListItem,
    'v-footer': Footer,
    'footer-item': FooterItem
  },
  methods: {
    onRefresh(done) {
      let beginData = this.beginData
      setTimeout(function() {
        for (var i = 0; i < 10; i++) {
          beginData.unshift({
            txt: Math.random().toString(16).slice(2, 9),
            time: new Date().toLocaleTimeString(),
            uid:new Date().getTime()
          });
        }
        done() // call done
      }, 1000)
    },
    onInfinite(done) {
      let listData = this.itemData;
      setTimeout(function() {
        for (var i = 0; i < 10; i++) {
          listData.push({
            txt: Math.random().toString(16).slice(1, 9),
            time: new Date().toLocaleTimeString()
          })
        }
        done() // call done()
      }, 1000)
    }
  }
}
</script>



