export default {
	bind(el, binding, vnode) {
		const isTouch = document.createElement('div').ontouchstart !== undefined;
		el.touching = false;
		el._point = (e) => {
			if (e.touches) {
				return {x: e.touches[0].clientX, y: e.touches[0].clientY}
			} else {
				return {x: e.pageX, y: e.pageY}
			}
		};
		el._start = (e) => {
			el.touching = true;
			const point = el._point(e);
			el.startPoint = point;
			el.startTime = +new Date();
			if (el.swipeStart)
				el.swipeStart(el.startPoint);
			el.diff = {
				x: 0,
				y: 0
			}
		};
		el._move = (e) => {
			if (!el.touching)
				return false
			e.preventDefault()
			const point = el._point(e)
			el.movePoint = point
			el.diff = {
				x: point.x - el.startPoint.x,
				y: point.y - el.startPoint.y
			}
			if (el.swipeMove)
				el.swipeMove(point, el.diff, + new Date() - el.startTime)
		};
		el._end = (e) => {
			if (el.swipeEnd)
				el.swipeEnd(el.movePoint, el.diff, + new Date() - el.startTime)
			el.touching = false
		};
		if (binding.arg == 'start') {
			el.swipeStart = binding.value
			if (isTouch) {
				el.addEventListener('touchstart', el._start);
			} else {
				el.addEventListener('mousedown', el._start);
			}
		}
		if (binding.arg == 'move') {
			el.swipeMove = binding.value
			if (isTouch) {
				el.addEventListener('touchmove', el._move);
			} else {
				el.addEventListener('mousemove', el._move)
			}
		}
		if (binding.arg == 'end') {
			el.swipeEnd = binding.value;
			if (isTouch) {
				el.addEventListener('touchend', el._end)
			} else {
				el.removeEventListener('mouseup', el._end)
			}
		}
	},
	unbind(el) {
		el.removeEventListener('touchstart', el._start)
		el.removeEventListener('touchmove', el._move)
		el.removeEventListener('touchend', el._end)
		el.removeEventListener('mousedown', el._start)
		el.removeEventListener('mousemove', el._move)
		el.removeEventListener('mouseup', el._end)
	}
}



