<template>
<div class="page yo-flex">
  <v-header title="首页"></v-header>
  <v-content>
    <v-search :text="searchTxt" :link="searchLink"></v-search>
    <v-align>
      <v-btn txt="打开Dialog" :click="open"></v-btn>
    </v-align>
  </v-content>
  <v-footer>
    <template v-for="item in footer">
      <footer-item :icon="item.icon" :text="item.text" :active="item.active" :link="item.link"></footer-item>
    </template>
  </v-footer>
  <v-dialog :show="show" v-on:pclose="close" title="dialog标题" content="测试内容晋升空间罚款骄傲会计法" :ok="ok" :isTitle="isTitle">
    <header slot="header">测试标题</header>
  </v-dialog>
</div>
</template>

<script>
import Header from '../components/header/header';
import Content from '../components/content/index';
import Dialog from '../components/dialog/dialog';
import Button from '../components/btn/btn';
import Align from '../components/align/align';
import Search from '../components/search/search';
import {
  Footer,
  FooterItem
} from '../components/footer/index';

export default {
  data() {
    return {
      msg: 'Welcome to use uikit',
      footer: [{
        icon: '&#xe680;',
        text:'搜索',
        active: false,
        link: 'search'
      }, {
        icon: '&#xe670;',
        text:'发现',
        active: true,
        link: 'find'
      }, {
        icon: '&#xe670;',
        text:'列表',
        active: false,
        link: 'list'
      }, {
        icon: '&#xe666;',
        text:'我的',
        active: false,
        link: 'side'
      }],
      show:true,
      isTitle:false,
      ok(){
        console.log('i am ok');
      },
      searchTxt:'搜索',
      searchLink:'search'
    }
  },
  components: {
    'v-header': Header,
    'v-content':Content,
    'v-dialog': Dialog,
    'v-btn': Button,
    'v-align': Align,
    'v-footer': Footer,
    'v-search': Search,
    'footer-item': FooterItem
  },
  methods:{
    open:function(){
      this.$data.show=true;
    },
    close:function(){
      this.$data.show=false;
    }
  }
}
</script>



