<template lang="html">
  <modal :show="show" v-on:pclose="close"  v-swipe:start="slideStart" v-swipe:move="slideMove" v-swipe:end="slideEnd">
    <section class="side ani">
      <div class="panel" v-bind:style="{ transform: 'translate3d('+x+'px, 0, 0)' }">
        <slot></slot>
      </div>
    </section>
  </modal>

</template>

<script>
import Modal from '../modal/modal';

export default {
  components: {
    Modal
  },
  data:function(){
    return {
      width: 0,
      touching: false,
      x: 0
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    slideStart (point) {
      this.width = this.$el.querySelector('.side').getBoundingClientRect().width
      this.touching = true;
    },
    slideMove (point, diff, time) {
      if (diff.x > 0) {
      } else {
        this.x = -Math.pow(-diff.x, 0.9)
        this.opacity = (this.width - Math.abs(this.x)) / this.width
      }
    },
    slideEnd (point, diff, time) {
      this.touching = false
      if (this.x < 0 && (Math.abs(this.x) > 80 || Math.abs(this.x) > 20 && time < 200)) {
        this.$emit('pclose');
        setTimeout(function () {
          this.x=0;
        }.bind(this), 300);
      } else {
        this.x = 0
      }
      this.opacity = 1
    },
    close() {
      this.$emit('pclose')
    }
  }
}
</script>



